#profile-card {
    width: 750px;
    min-height: 100% !important;
    max-height: fit-content;
    border-radius: 20px !important;
    padding: 25px;
    margin: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: var(--profile-card-bg, #f4f4f5) !important;
}

.authenticator-buttons {
    text-align: start;
    display: grid !important;
    border-radius: 4px !important;
    grid-template-columns: 1fr 20fr;
}

#profile-card-header {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    gap: 10px;
}

#profile-container {
    padding: 24px 12px;
    /* Match SettingsPage viewport sizing and scrolling */
    height: calc(100vh - 120px) !important;
    max-height: calc(100vh - 80px) !important;
    width: 100% !important;
    text-align: center;
    overflow-y: scroll !important;
    -webkit-overflow-scrolling: touch;
}

#profile-card-footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 10px;
}

#profile-card-header div {
    border-radius: 10px;
    background-color: var(--profile-input-bg, #ffffff);
}

/* Base (light) variables */
:root {
    --profile-card-bg: #d6d6d6;
    /* keep cards light */
    --profile-surface: #F5F2E7;
    --profile-border: #e6e6eb;
    /* New distinct page background (slightly tinted gray-violet) */
    --profile-page-bg: #f4f4f5;
    /* lighter gray so cards still pop via border+shadow */
}

.provider-type {
    font-weight: 500 !important;
    font-size: 1.2rem !important;
}

[data-theme='dark'] {
    --profile-card-bg: #131924;
    /* card navy */
    --profile-surface: #10151d;
    --profile-border: #243040;
    /* Darker page background to contrast with card (#0b0f1a similar to settings dark) */
    --profile-page-bg: #0b0f1a;
}

#emailField input:valid {
    border-color: green;
}

#emailField input:invalid {
    border-color: red;
}

/*personal-information-card*/
#personal-information-card {
    border-radius: 16px;
    margin: auto;
    width: min(880px, 92%) !important;
    background: var(--pro-card) !important;
    border: 1px solid var(--pro-border) !important;
    color: var(--pro-text);
    box-shadow: 0 8px 22px rgba(0, 0, 0, 0.08);
}

.username-server-uuid {
    width: 100%;
    display: flex !important;
    flex-direction: row !important;
    justify-content: center !important;
    align-items: stretch !important;
    gap: 20px;
    /* زيادة المسافة بين الحقول */

}

.username-server-uuid>* {
    flex: 1 1 0;
    min-width: 0;
}

.username-server-uuid .MuiTextField-root {
    flex: 1 1 0;
    min-width: 0;
}

.email-phone {
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;

}

/* Ensure TextFields never overflow card width */
#personal-information-card .MuiTextField-root {
    width: 100%;
    max-width: 100%;
}

#personal-information-card .MuiTextField-root .MuiInputBase-root {
    width: 100%;
    box-sizing: border-box;
}

#personal-information-card .MuiFormControl-root {
    width: 100%;
    max-width: 100%;
}

#personal-card-footer {
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    justify-content: center;
}

#personal-card-footer button {
    width: 80%;
    height: 64px;
    border-radius: 10px;
    background: #1976D2;
    color: white;
    text-transform: capitalize;
    font-size: 18px;
}

.section-provider-key .header-provider-key {
    display: flex;
    justify-content: space-between;
}

