/* ===== Enhanced UI additions (Profile) ===== */
:root {
    /* Light theme defaults aligned with app */
    --pro-bg: #f4f4f5;
    /* page background */
    --pro-surface: #ffffff;
    /* input surfaces */
    --pro-card: #d6d6d6;
    /* card surfaces */
    --pro-border: #e5e7eb;
    /* light divider */
    --pro-text: #0f172a;
    /* dark text */
    --pro-muted: #64748b;
    /* secondary text */
    --pro-accent: #1976D2;
    /* primary accent */
    --pro-primary: #115293;
    /* primary hover */
    --pro-success: #16a34a;
    /* green-600 */
}

@media (prefers-color-scheme: dark) {
    :root {
        --pro-bg: #0f1115;
        --pro-surface: #0f172a;
        --pro-card: #0f172a;
        --pro-border: #1f2937;
        --pro-text: #e5e7eb;
        --pro-muted: #94a3b8;
        --pro-accent: #1976D2;
        --pro-primary: #115293;
    }
}

/* App theme toggle support */
[data-theme='dark'] {
    --pro-bg: #0f1115;
    --pro-surface: #0f172a;
    --pro-card: #0f172a;
    --pro-border: #1f2937;
    --pro-text: #e5e7eb;
    --pro-muted: #94a3b8;
    --pro-accent: #1976D2;
    --pro-primary: #115293;
}

