/* Providers header spacing */
.provider-container {
    max-width: min(960px, 92%);
    margin: 12px auto;
    align-items: stretch;
}

.provider-container .MuiTypography-root {
    text-align: left;
}

/* Primary pill buttons in this page */
#profile-container>.MuiButton-root {
    border-radius: 10px !important;
    height: 56px !important;
    padding: 10px 18px !important;
    font-weight: 600 !important;
    letter-spacing: .2px;
    color: #fff !important;
    background: #1976D2 !important;
    border: 1px solid rgba(25, 118, 210, .35) !important;
    box-shadow: 0 6px 14px rgba(0, 0, 0, .15) !important;
}

#profile-container>.MuiButton-root:hover {
    filter: brightness(1.03);
    transform: translateY(-1px);
}

#profile-container>.MuiButton-root svg {
    margin-inline-end: 8px;
}

/* Save button stays strong purple pill */
#personal-card-footer button {
    border: 1px solid rgba(25, 118, 210, .35);
}

/* Archive/Delete button: keep destructive intent but polish it */
#profile-container .MuiButton-root[style*="background-color: red"] {
    border-radius: 10px !important;
    height: 56px !important;
    padding: 10px 18px !important;
    font-weight: 600 !important;
    letter-spacing: .2px;
    box-shadow: 0 8px 18px rgba(239, 68, 68, .25) !important;
}

#profile-container .MuiButton-root[style*="background-color: red"]:hover {
    filter: brightness(1.05);
    transform: translateY(-1px);
}

/* Inputs themed globally in index.css; keep only spacing/surface where needed */
.text-field {
    margin-top: 4px !important;
    background-color: var(--input-bg);
}

/* Autocomplete dropdowns */
.MuiAutocomplete-popper .MuiPaper-root {
    background: var(--pro-surface) !important;
    color: var(--pro-text);
    border: 1px solid var(--pro-border);
}

.MuiAutocomplete-option {
    color: var(--pro-text);
}

/* Section titles: spacing mimic */
#profile-container .MuiTypography-root[style*="textDecoration: underline"],
#profile-container .MuiTypography-root[style*="text-decoration: underline"] {
    text-decoration-thickness: 2px !important;
    text-underline-offset: 6px !important;
}

/* ===== Responsive adjustments for profile page ===== */
:root {
    --profile-max-width: 750px;
}

@media (max-width:1200px) {
    :root {
        --profile-max-width: 680px;
    }

    #profile-card {
        width: calc(min(88%, var(--profile-max-width)));
        padding: 20px;
    }
}

@media (max-width:900px) {
    :root {
        --profile-max-width: 640px;
    }

    #profile-card {
        width: calc(94%);
        min-height: auto !important;
        padding: 16px;
    }

    #profile-container {
        height: auto !important;
        overflow: visible;
    }

    #profile-card-footer {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, auto);
        gap: 8px;
    }

    /* Keep first row in a single line above 640px */

    .section-provider-key {
        width: 100% !important;
    }
}

@media (max-width:640px) {
    /* Below 640px, stack first row fields */
    .username-server-uuid {
        flex-direction: column;
        gap: 10px;
    }
    :root {
        --profile-max-width: 100%;
    }

    #profile-card {
        width: 96%;
        border-radius: 12px !important;
        padding: 12px;
    }

    #personal-information-card {
        width: 100%;
        padding: 18px;
    }

    .authenticator-buttons {
        grid-template-columns: 1fr 1fr;
        gap: 8px;
    }

    #profile-card-header {
        gap: 8px;
    }

    .provider-container {
        gap: 6px;
    }

    .models-grid {
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    }

    .model-tile {
        padding: 10px;
    }

    #personal-card-footer button {
        height: 56px;
        width: 100%;
        border-radius: 10px;
        font-size: 16px;
    }
}

@media (max-width:420px) {
    .category-search-field {
        padding: 6px 8px !important;
    }

    .provider-label {
        font-size: 0.9rem;
    }
}

